.advantages {
    padding-top: 40px;
    padding-bottom: 40px;
}

.advantages__item {
    display: flex;
    align-items: center;
}

.advantages__item::before {
    display: inline-block;
    margin-right: 12px;
    content: " ";
    background-repeat: no-repeat;
    background-size: contain;
    width: 59px;
    height: 60px;
}

.advantages__item_female-student::before {
    background-image: url('../images/advantages/female-student-mobile.svg');
}

.advantages__item_learning::before {
    background-image: url('../images/advantages/learning-mobile.svg');
}

.advantages__item_insurance::before {
    background-image: url('../images/advantages/insurance-mobile.svg');
}

@media screen and (max-width:767.98px) {
    .advantages__item:not(:last-child) {
        margin-bottom: 24px;
    }
}

.advantages__title {
    
    @include font(14px, 700, 23px);
    letter-spacing: 0.08em;
    color: var(--dark-text-color);
}

// ============= 768+ =============

@media screen and (min-width: 768px) {
    
    .advantages {
        padding-bottom: 80px;
    }

    .advantages__list {
        display: flex;
        justify-content: center;
        gap: 21px;
    }

}

@media screen and (min-width: 768px) and (max-width: 1679px) {
    .advantages__title {
        width: 153px;
    }
}

// ================ 1680+ ================

@media screen and (min-width: 1680px) {

    .advantages__list {
        justify-content: left;
        gap: 112px;
    }

    .advantages__item {
        width: 448px;
    }

    .advantages__item::before {
        width: 100px;
        height: 102px;
        margin: 0;
    }

    .advantages__item_female-student::before {
        background-image: url('../images/advantages/female-student-desktop.svg');
    }
    
    .advantages__item_learning::before {
        background-image: url('../images/advantages/learning-desktop.svg');
    }
    
    .advantages__item_insurance::before {
        background-image: url('../images/advantages/insurance-desktop.svg');
    }

    .advantages__title {
        margin-left: -18px;
        @include font(20px, 700, 33px)
    }
}